//清浮动
.clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
  }

  &:after {
    clear: both;
    overflow: hidden;
  }
}

.wrapper {
  padding: 15px 5px;
  &:hover {
    background: #dfecfb;
  }
  :global {
    .ant-select,
    .ant-input {
      width: 100%;
    }
  }
}

.select {
  display: block;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #d9d9d9;
  border-top-width: 1.02px;
  border-radius: 4px;
  outline: none;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  user-select: none;
  padding-left: 11px;
  line-height: 30px;
}
.content {
  width: 500px;
  background: #fff;
  border: 1px solid @border;
  padding: 20px;
  border-radius: 4px;
}

.center {
  text-align: center;
  line-height: 32px;
}

.metadataBox {
  max-height: 90vh;
  overflow: auto;
  span {
    display: inline-block;
    width: 30%;
    border-radius: 2px;
    margin: 5px 1.5%;
    padding: 5px 10px;
    &:hover,
    &.active {
      background: @primary;
      color: #fff;
    }
  }
}

.filter {
  padding-left: 50px;
}
